nav {
    margin-top: 35px;
    margin-bottom: 20px;
    @include breakpoint(xs) {
        margin-top: 20px;
        
    }
    .nav-wrapper {
        text-transform: uppercase;
        color: #fff;
        font-family: "Century Gothic", sans-serif;
        font-size: 15px;
        font-weight: bold;
        text-align: center;
        background-image: url(../img/nav-bg.png);
        background-size: 100% 100%;
        padding: 12px 0;
        a {
            text-shadow: 0px -2px 0px rgba(255, 138, 0, 0.5);
            display: inline-block;
            width: (100% / 6);
            color: inherit!important;
            text-decoration: none;
            border-right:1px solid #ffc900;
            border-left:1px solid #ff8a00;
            @include breakpoint(xs) {
                padding: 3px 0;
                width: (100%/3);
            }
            @include breakpoint($min:0px,$max:500px) {
                padding: 3px 0;
                width: (100%/2);
            }
        }
    }
    
}