footer {
    font-family: 'Arial', sans-serif;
    font-size: 12px;
    .footer-wrapper {
        padding: 8px 0 0;
        background-color: #dfdfdf;
        border-radius: 5px 5px 0 0;
        position: relative;
    }
    .top-footer {
        display: table;
        border-bottom: 1px solid #c1c1c1;
        padding-bottom: 10px;
        
        > .footer-info-col {
            width: 30%;
            display: table-cell;
            border-right: 1px solid #c1c1c1;
            border-left: 1px solid #ffffff;
            padding: 10px 10px 0;
            vertical-align: top;
            &:first-child {
                border-left: 0;
            }
            &:nth-child(3) {
                border-right: 0;
            }
        }
        .info {
            margin: 10px 0 0;
            color: #666666;
            line-height: 20px;
            p {
                margin: 0;
            }
            a {
                color: inherit!important;
                text-decoration: none;
            }
            
        }
        ul.info {
            padding-left: 0;
            list-style:none;
        }
        .info-label {
            font-family: 'Century Gothic',sans-serif;
            font-weight: bold;
            font-size: 14px;
            text-transform: uppercase;
        }
        .info-header {
            font-size: 21px;
            font-family: "Century Gothic",sans-serif;
            color: rgb(255, 114, 0);
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: 17px;
        }
      
    }
    .bottom-footer {
        border-top: 1px solid #ffffff;
        font-size: 11px;
        padding: 10px;
        .links {
            float: left;
            a {
                text-decoration: none;
                color: #060606;
                padding: 0 5px;
                border-right: 1px solid;
                &:first-child {
                    padding-left: 0;
                }
                &:last-child {
                    border-right: 0;
                }
            }
        }
        .copyright {
            float: right;
            color: #666666;
        }
    }
    .newsleter-col {
        .light-input {
            vertical-align: top;
            width: 225px;
            height: 24px;
            margin-right: 8px;
            padding-left: 5px;
            @include breakpoint($min:0px,$max:360px) {
                width: 100%;
                & ~ .btn-primary {
                    margin-top: 10px;
                }
            }
        }
    }
}
footer {
    @include breakpoint(xs) {
        .bottom-footer {
            .links {
                display: block;
                float: none;
                text-align: center;
            }
            .copyright {
                margin-top: 10px;
                float: none;
                text-align: center;
            }
        }
        
        .top-footer {
            display: block;
           
            > .footer-info-col {
                display: block;
                width: auto;
                border: 0;
                border-bottom: 1px solid #c1c1c1;
                border-top: 1px solid #ffffff;
                padding-bottom: 10px;
                &:first-child {
                    border-top:0;
                }
                &:nth-child(3) {
                    border-bottom: 0;
                }
            }
        }
    }
}