header {
    .logo-wraper {
        float: left;
        @include breakpoint(xs) {
            float: none;
            & ~ .clearfix {
                display: none;
            }
        }
        text-align: center;
    }
    .company-logo {
        display: inline-block;
        position: relative;
        font-size: 30px;
        font-family: 'Century Gothic', sans-serif;
        text-transform: uppercase;
        font-weight: bold;
        margin-top: 35px;
        padding-left: 33px;
        letter-spacing: 0.015em;
       
        img {
            position: absolute;
            width: 56px;
            top: -16px;
            left: 0;
        }
    }
    .search-box {
        float: right;
        margin-top: 46px;
        @include breakpoint(xs) {
            float: none;
            text-align: center;
            margin-top: 30px;
    
        }
        > * {
            vertical-align: middle;
        }
        
    }
    .input-group {
        position: relative;
        display: inline-block;
        margin-right: 9px;
        img {
            top: 50%;
            padding:2px 7px;
            bottom: 0;
            right: 0;
            position: absolute;
            width: 11px;
            border-left: 1px solid #b2b2b2;
            @include transform(translate(0,-50%));
            @include box-sizing(content-box);
        }
        .light-input {
            width: 198px;
            height: 25px;
            padding-right: 30px;
        }
    }
    .flags {
        border-left: 1px solid #b2b2b2;
        display: inline-block;
        padding: 2px 0 1px 8px;
        a {
            padding-left: 6px;
            display: inline-block;
            vertical-align: middle;
            &:first-child {
                padding-left: 0;
            }
            img {
                max-width: 52px;
            }
        }
    }
   
}
