@mixin transform($value) {
    -webkit-transform: $value;
    -moz-transform: $value;
    -ms-transform: $value;
    -o-transform: $value;
    transform: $value;
}
@mixin box-sizing($value) {
    -webkit-box-sizing: $value;
    -moz-box-sizing: $value;
    box-sizing: $value;
}
@mixin breakpoint($min: 0, $max: 0) {
    $type: type-of($min);
    @if $type == string {
        $class: $min;
        @if $class == xs {
            @media (max-width: 767px) { @content; }
        }
    
        @else if $class == sm {
            @media (min-width: 768px) { @content; }
        }
    
        @else if $class == md {
            @media (min-width: 992px) { @content; }
        }
    
        @else if $class == lg {
            @media (min-width: 1200px) { @content; }
        }
    
        @else {
            @warn "Breakpoint mixin supports: xs, sm, md, lg";
        }
    }
    @else if $type == number {
        $query: "all" !default;
        @if $min != 0 and $max != 0 {
            $query: "(min-width: #{$min}) and (max-width: #{$max})";
        }
        @else if $min != 0 and $max == 0 {
            $query: "(min-width: #{$min})";
        }
        @else if $min == 0 and $max != 0 {
            $query: "(max-width: #{$max})";
        }
        @media #{$query} {
            @content;
        }
    }
}
*, *:before, *:after {
   @include box-sizing(inherit);
}
.row::before,
.row::after {
    display: table;
    content: " ";
    clear: both;
}
.column {
    float: left;
}
.container {
    margin: 0 auto;
    padding: 0 10px;
    max-width: 1000px;
}
html {
    min-height: 100%;
    @include box-sizing(border-box);
}
body {
    margin: 0;
    min-height: 100%;
    font-size: 12px;
    font-family: 'Arial', sans-serif;
    background-image: url(../img/background.png);
}
.clearfix:after {
    clear: both;
    content: ".";
    display: block;
    height: 0;
    visibility: hidden;
}
.clearfix {
    display: inline-block;
}
.clearfix {
    display: block;
}
a {
    color: inherit;
    text-decoration: none;
}
a:hover {
    opacity: 0.67;
}
.gradient {
    background-image: url(../img/nav-bg.png);
    background-size: 100% 100%;
}