.don-slider {
    position: relative;
    height: 307px;
    overflow: hidden;
    border: 1px solid rgb(255, 138, 0);
    border-radius: 5px;
    box-shadow: inset 2.046px 2.194px 0px 0px rgba(255, 201, 0, 0.004);
    
    
    .btn-next {
        right: 10px!important;
    }
    .btn-prev {
        left: 10px!important;
    }
    
    ul {
        position: relative;
        margin: 0;
        padding: 0;
        height: 100%;
        list-style: none;
        
        li {
            position: relative;
            display: block;
            float: left;
            margin: 0;
            padding: 0;
            height: 100%;
            background: #ccc;
            text-align: center;
            line-height: 300px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                
            }
        }
    }
}
.slider-overplay {
    background-color: #000;
    display: table;
    color: #ffffff;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    .caption {
        padding: 10px 10px 10px;
        display: table-cell;
        @include breakpoint($min:0px,$max:500px) {
            display: none;
        }
    }
    .indicator-container {
        text-align: center;
        padding: 10px;
        border-left: 1px solid #ffffff;
        display: table-cell;
        vertical-align: middle;
        min-width: 140px;
        .dot {
            width: 21px;
            height: 21px;
            padding: 3px 7px;
            margin-right: 7px;
        }
    }
}
.slide-control {
    position: absolute;
    top: 50%;
    @include transform(translateY(-50%));
    cursor: pointer;
    cursor: hand;
    img {
        max-width: 31px;
    }
    &.btn-next {
        right: 0;
    }
    &.btn-prev {
        left: 0;
    }
}
.indicator {
    .dot {
        margin-right:3px;
        display: inline-block;
        border-radius: 100%;
        width: 9px;
        height: 9px;
        background-color: rgb(107, 104, 94);
        box-shadow: inset 0.616px -0.788px 0px 0px rgba(192, 186, 166, 0.75);
        cursor: pointer;
        cursor: hand;
        color: #fff;
        &.active,&:hover,&:active {
            @extend .gradient;
        }
    }
}

