.page-home {
    .news-event-partner {
        margin: 20px 0;
    }
    .welcome-article {
        padding: 10px;
        background-color: #dfdfdf;
        border-radius: 5px;
        position: relative;
        display: table;
        table-layout: fixed;
        margin-top: 20px;
        @include breakpoint(xs) {
            display: block;
        }
        p {
            margin-top: 9px;
            margin-bottom: 10px;
            font-size: 11px;
            text-align: justify;
            line-height: 18px;
        }
        .article-content {
            display: table-cell;
            vertical-align: middle;
            padding-right: 25px;
            @include breakpoint(xs) {
                display: block;
                padding-right: 0;
            }
            
        }
        .article-slider {
            display: table-cell;
            vertical-align: middle;
            @include breakpoint(xs) {
                display: block;
            }
            .indicator {
                position: absolute;
                right: 10px;
                bottom: 10px;
            }
            .wrapper {
                margin: auto;
                width: 242px;
                position: relative;
                img {
                    width: 100%;
                }
            }
        }
        a {
            font-size: 11px;
            text-decoration: underline;
            color: #0096ff;
        }
    }
    h2 {
        font-size: 31px;
        font-family: "Century Gothic",sans-serif;
        color: rgb(255, 105, 0);
        font-weight: bold;
        text-align: left;
        margin: 5px 0;
        letter-spacing: 0.008em;
    }
    .news-events {
        width: 50%;
        padding-right: 10px;
        float: left;
        @include breakpoint($min:0px,$max:910px) {
            width: (100%);
            padding-right: 0;
        }
    }
    .partners {
        width: 50%;
        padding-left: 10px;
        float: right;
        @include breakpoint($min:0px,$max:910px) {
            width: (100%);
            padding-left: 0;
            margin: 20px 0 0px;
        }
        .row {
            margin: 10px 0 0;
            position: relative;
            padding: 0 38px;
            
        }
        .btn-next {
            right: 10px;
        }
        .btn-prev {
            left: 10px;
        }
        .column {
            text-align: center;
            padding: 10px 10px;
            width: (100%/3);
            @include breakpoint($min:350px,$max:500px) {
                width: (100%/2);
            }
            @include breakpoint($min:0px,$max:349px) {
                width: (100%/1);
            }
            img {
                border: 1px solid #cdcdcd;
                border-radius: 3px;
                width: 113px;
            }
        }
    }
}


.grey-panel {
    padding: 8px 10px;
    background-color: #dfdfdf;
    border-radius: 5px;
    position: relative;
    .panel-header {
        margin: 0 -10px;
        border-bottom: 1px solid #c1c1c1;
        .panel-header-title {
            padding-left: 10px;
            float: left;
            font-size: 21px;
            font-family: "Century Gothic", sans-serif;
            color: rgb(255, 114, 0);
            font-weight: bold;
            text-transform: uppercase;
            padding-bottom: 8px;
        }
        a.more {
            float: right;
            margin-top: 10px;
            padding-right: 17px;
            font-size: 11px;
            text-decoration: underline;
            color: #0096ff;
        }
    }
    .panel-content {
        margin: 0 -10px;
        border-top: 1px solid #ffffff;
    }
    
}
.news-row {
    margin: 20px 10px 10px;
    .news-item {
        margin: 15px 0 0;
    }
}
.news-item {
    font-size: 11px;
    .news-content {
        padding: 0 15px;
        line-height: 14px;
        .news-title {
            display: table-cell;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            padding-right: 15px;
            color: rgb(255, 105, 0);
            font-weight: bold;
            &:before {
                content: '‚ùÖ';
                font-family: 'Arial', sans-serif;
                color: #060606;
                position: absolute;
                left: -15px;
                top: 0;
            }
        }
        .news-head {
            display: table;
            table-layout: fixed;
            width: 100%;
            position: relative;
        }
        .publish-date {
            display: table-cell;
            color: #666666;
            width: 65px;
            span {
                padding-left: 10px;
                border-left: 1px solid #666666;
            }
        }
    }
}
.news-item.is-avatar {
    padding: 10px;
    background-color: #f0eeee;
    border-radius: 5px;
    position: relative;
    border: 1px solid #ffffff;
    display: table;
    @include breakpoint($max:500px) {
        display: block!important;
        .news-avatar {
            display: block!important;
            text-align: center;
            margin-bottom: 10px;
        }
        .news-content {
            display: block;
            padding: 0 4px!important;
        }
    }
    .news-avatar {
        display: table-cell;
        vertical-align: top;
        img {
            max-width: 133px;
        }
    }
    .news-title {
        &:before {
            content: '';
        }
    }
    .news-content {
        display: table-cell;
        padding: 0 10px;
    }
    figure {
        
    }
}