.light-input {
    border: 1px solid rgb(207, 205, 197);
    border-radius: 5px;
    background-image: url(../img/light-input-bg.png);
    background-size: 100% 100%;
    padding: 0 7px;
    font-size: 11px;
    //background-image: -moz-linear-gradient( 90deg, rgb(244,243,241) 0%, rgb(255,255,255) 100%);
    //background-image: -webkit-linear-gradient( 90deg, rgb(244,243,241) 0%, rgb(255,255,255) 100%);
    //background-image: -ms-linear-gradient( 90deg, rgb(244,243,241) 0%, rgb(255,255,255) 100%);
    //box-shadow: inset 0px -2px 0px 0px rgba(255, 255, 255, 0.004);
    input {
        background-color: transparent;
        border: 0;
    }
    a.more {
        font-family: 'Arial', sans-serif;
        color:#0096ff;
        text-decoration: underline;
    }
}

.btn-primary {
    cursor: hand;
    cursor: pointer;
    border-radius: 5px;
    border: 1px solid rgb(255, 105, 0);
    box-shadow: 1px 1.732px 3px 0 rgba(0, 0, 0, 0.3);
    font-size: 14px;
    font-family: "Century Gothic", sans-serif;
    color: #ffffff;
    font-weight: bold;
    text-shadow: 0px -2px 0px rgba(255, 105, 0, 0.63);
    display: inline-block;
    padding: 4px 8px;
    @extend .gradient;
    
}